
"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { detailedPatients as initialPatients, Patient } from "@/lib/constants";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ArrowRight } from "lucide-react";
import { CreatePatientDialog } from "@/components/create-patient-dialog";
import { useToast } from "@/hooks/use-toast";


export default function Page() {
    const [patients, setPatients] = useState(initialPatients);
    const [isDialogOpen, setDialogOpen] = useState(false);
    const { toast } = useToast();

    const handlePatientSaved = (newPatient: Omit<Patient, 'id' | 'medicalHistory' | 'prescriptions' | 'lastVisit' | 'labTests'>) => {
        const patientToAdd: Patient = {
            ...newPatient,
            id: `p-${Date.now()}`,
            lastVisit: new Date().toISOString().split('T')[0], // Set current date as last visit
            medicalHistory: [{
                date: new Date().toISOString().split('T')[0],
                event: "Patient Registration",
                details: "Patient registered in the system.",
                doctor: "Admin"
            }],
            prescriptions: [],
            labTests: []
        };
        
        // This is a mock implementation. In a real app, this would be an API call.
        initialPatients.unshift(patientToAdd);
        setPatients([...initialPatients]);

        toast({
            title: "Patient Created",
            description: `${newPatient.name} has been successfully added to the system.`
        });
        setDialogOpen(false);
    };

    return (
        <>
            <Card>
                <CardHeader>
                    <div className="flex justify-between items-center">
                        <div>
                            <CardTitle>All Patients</CardTitle>
                            <CardDescription>A list of all patients in the system.</CardDescription>
                        </div>
                        <Button onClick={() => setDialogOpen(true)}>Create Patient</Button>
                    </div>
                </CardHeader>
                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Name</TableHead>
                                <TableHead>Condition</TableHead>
                                <TableHead>Last Visit</TableHead>
                                <TableHead><span className="sr-only">Actions</span></TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {patients.map((patient) => (
                                <TableRow key={patient.id}>
                                    <TableCell className="font-medium flex items-center gap-3">
                                        <Avatar>
                                            <AvatarImage src={`https://placehold.co/40x40.png?text=${patient.name.charAt(0)}`} alt={patient.name} data-ai-hint="person" />
                                            <AvatarFallback>{patient.name.charAt(0)}</AvatarFallback>
                                        </Avatar>
                                        {patient.name}
                                    </TableCell>
                                    <TableCell>
                                        <Badge variant={patient.condition === 'Critical' ? 'destructive' : 'secondary'}>{patient.condition}</Badge>
                                    </TableCell>
                                    <TableCell>{patient.lastVisit}</TableCell>
                                    <TableCell className="text-right">
                                        <Button asChild variant="ghost" size="icon">
                                            <Link href={`/admin/patients/${patient.id}`}>
                                                <ArrowRight className="w-4 h-4" />
                                                <span className="sr-only">View Patient</span>
                                            </Link>
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
            <CreatePatientDialog
                isOpen={isDialogOpen}
                onClose={() => setDialogOpen(false)}
                onPatientSaved={handlePatientSaved}
            />
        </>
    )
}
