
"use client";

import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Patient } from "@/lib/constants";
import { useEffect } from "react";

const formSchema = z.object({
  name: z.string().min(2, "Name must be at least 2 characters."),
  condition: z.string().min(2, "Condition must be at least 2 characters."),
});

type FormData = z.infer<typeof formSchema>;
type PatientSaveData = Omit<Patient, 'id' | 'medicalHistory' | 'prescriptions' | 'lastVisit' | 'labTests'>;

interface CreatePatientDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onPatientSaved: (patient: PatientSaveData) => void;
}

export function CreatePatientDialog({ isOpen, onClose, onPatientSaved }: CreatePatientDialogProps) {
  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: "",
      condition: "Stable",
    },
  });

  useEffect(() => {
    if (isOpen) {
        form.reset({
            name: "",
            condition: "Stable",
        });
    }
  }, [isOpen, form]);


  function onSubmit(values: FormData) {
    onPatientSaved(values);
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Create New Patient</DialogTitle>
           <DialogDescription>
            Fill in the details to register a new patient in the system.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Full Name</FormLabel>
                  <FormControl>
                    <Input placeholder="Jane Smith" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="condition"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Initial Condition</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Stable, Critical" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <DialogFooter>
              <Button type="button" variant="ghost" onClick={onClose}>Cancel</Button>
              <Button type="submit">Create Patient</Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
